import styled from "styled-components";
import {Link, useLocation, useNavigate} from "react-router-dom";
import {useEffect, useState} from "react";
import axiosInstance from "../pages/axiosInstance";

const Header = ({openModal}) => {
    const navigate = useNavigate();
    const location = useLocation();
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const [searchId, setSearchId] = useState("");
    const [role, setRole] = useState("");

    const checkLoginStatus = async () => {
        const token = localStorage.getItem("accessToken"); //     

        if (token) {
            setIsLoggedIn(true);
            try {
                const response = await axiosInstance.get('/token/decode');
                const {role} = response.data; //   
                setRole(role);
            } catch (error) {
                console.error("   :", error);
                handleLogout(); //  
            }
        } else {
            setIsLoggedIn(false);
            setRole(""); //  
        }
    };

    useEffect(() => {
        checkLoginStatus(); //     
        const intervalId = setInterval(checkLoginStatus, 60000); // 1   

        return () => clearInterval(intervalId); //    interval 
    });

    const handleLogout = () => {
        try {
            //     ( )
            axiosInstance.post("/join/logout", null, {withCredentials: true})
                .then(() => {
                    //       ID 
                    localStorage.removeItem("accessToken");
                    localStorage.removeItem("memberId");

                    //   
                    window.location.href = "/";
                })
                .catch(error => {
                    console.error("Logout failed:", error);
                    alert(" .  .");
                });
        } catch (error) {
            console.error("Logout failed:", error);
            alert(" .  .");
        }
    };


    const handleSearch = () => {
        if (searchId.trim()) {
            navigate(`/courses?searchId=${encodeURIComponent(searchId)}`);
        }
    };

    const isCourseDetailPage = location.pathname.startsWith("/courses/");

    return (
        <NavBar>
            <LeftSection>
                {isCourseDetailPage && (
                    <SearchBar>
                        <input
                            type="text"
                            placeholder=""
                            value={searchId}
                            onChange={(e) => setSearchId(e.target.value)}
                            onKeyPress={(e) => {
                                if (e.key === 'Enter') handleSearch();
                            }}
                        />
                        <button onClick={handleSearch}></button>
                    </SearchBar>
                )}
            </LeftSection>
            <LogoWrapper onClick={() => navigate("/courses")}>
                <Logo>Learner</Logo>
            </LogoWrapper>

            <RightSection>
                {isLoggedIn ? (
                    <>
                        <NavItem onClick={() => navigate('/alarm/list')}></NavItem>
                        <NavItem onClick={() => navigate('/inquiries')}></NavItem>
                        <NavItem onClick={() => setIsMenuOpen(!isMenuOpen)}></NavItem>
                        {isMenuOpen && (
                            <SubMenu>
                                <SubMenuItem onClick={() => navigate('/')}></SubMenuItem>
                                {role === 'ROLE_INSTRUCTOR' && (
                                    <SubMenuItem onClick={() => navigate('/courses/list')}> </SubMenuItem>
                                )}
                                <SubMenuItem onClick={() => navigate('/orders')}></SubMenuItem>
                                <SubMenuItem onClick={() => navigate('/edit-profile')}> </SubMenuItem>
                                <SubMenuItem onClick={() => navigate('/my-courses')}>  </SubMenuItem>
                                <SubMenuItem onClick={handleLogout}></SubMenuItem>
                            </SubMenu>
                        )}
                        {role === "ROLE_ADMIN" && (
                            <NavItem onClick={() => navigate('/admin/courses-management')}>
                                 
                            </NavItem>
                        )}
                    </>
                ) : (
                    <Menu>
                        <StyledButton onClick={openModal}></StyledButton>
                        <Link to="/signup">
                            <StyledButton></StyledButton>
                        </Link>
                    </Menu>
                )}
            </RightSection>
        </NavBar>
    );
};

export default Header;

//   ( )

const NavBar = styled.nav`
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    padding: 0.5rem 1rem;
    height: 60px;
    border-bottom: 1px solid #ddd;
    margin: 0 auto;
    position: relative;
    overflow: visible;
`;

const LeftSection = styled.div`
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-right: auto; /* LeftSection   */
`;

const LogoWrapper = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    cursor: pointer;
`;

const RightSection = styled.div`
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-left: auto;
    position: relative;
`;

const Logo = styled.h1`
    font-size: 1.5rem;
    color: #3cb371;
    cursor: pointer;
`;

const Menu = styled.div`
    display: flex;
    gap: 1rem;
`;

const StyledButton = styled.button`
    background-color: #3cb371;
    color: white;
    border: 2px solid #3cb371;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    cursor: pointer;

    &:hover {
        background-color: white;
        color: #3cb371;
    }
`;

const NavItem = styled.span`
    cursor: pointer;
    padding: 0.5rem 1rem;
    position: relative;
    border-radius: 5px;

    &:hover {
        background-color: #3cb371;
        color: white;
    }
`;

const SubMenu = styled.div`
    position: absolute;
    top: 100%; /* NavItem     */
    left: 0;
    display: flex;
    flex-direction: column;
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin-top: 0.5rem;
    z-index: 1000; /*       */
    width: 150px;
    overflow: visible; /*       */
`;

const SubMenuItem = styled(NavItem)`
    padding: 0.5rem 1rem;

    &:hover {
        background-color: #f0f0f0;
    }
`;

const SearchBar = styled.div`
    display: flex;
    align-items: center;
    margin-left: 1rem;

    input {
        padding: 0.5rem;
        border-radius: 20px;
        border: 1px solid #ddd;
        width: 250px; /*    */
        margin-right: 0.5rem;
    }

    button {
        background: none;
        border: none;
        font-size: 1.2rem;
        cursor: pointer;
    }
`;
