import React, {useEffect, useState} from "react";
import {useNavigate, useParams} from "react-router-dom";
import axios from "../axiosInstance";
import styled from "styled-components";

const OrderUpdate = () => {
    const {orderId} = useParams();
    const [order, setOrder] = useState(null);
    const [courses, setCourses] = useState([]);
    const [selectedCourseId, setSelectedCourseId] = useState(""); //   ID 
    const [error, setError] = useState(null);
    const navigate = useNavigate();

    useEffect(() => {
        const fetchOrder = async () => {
            try {
                const response = await axios.get(`http://localhost:8080/order/${orderId}`, {withCredentials: true});
                setOrder(response.data);
            } catch (error) {
                console.error("     :", error);
                setError("    .");
            }
        };

        const fetchCourses = async () => {
            try {
                const response = await axios.get('http://localhost:8080/course/list', {withCredentials: true});
                setCourses(response.data);
            } catch (error) {
                console.error("     :", error);
                setError("    .");
            }
        };

        fetchOrder();
        fetchCourses();
    }, [orderId]);

    const handleCourseChange = (e) => {
        console.log("  ID:", e.target.value); //  
        setSelectedCourseId(e.target.value); //   ID 
    };

    const handleAddCourse = () => {
        console.log("  "); //  
        console.log("selectedCourseId", selectedCourseId);
        if (!selectedCourseId) {
            console.log("  ."); //   
            return;
        }

        const course = courses.find(c => c.courseId === Number(selectedCourseId));
        console.log(" :", course); //    
        if (course) {
            //      
            const exists = order.orderItemDTOList.some(item => item.courseId === course.courseId);
            if (exists) {
                console.log("  ."); //    
                return; //     
            }

            const updatedOrderItems = [...order.orderItemDTOList, {
                courseId: course.courseId,
                price: course.coursePrice
            }];
            const newTotalPrice = updatedOrderItems.reduce((total, item) => total + item.price, 0);

            console.log("  :", updatedOrderItems); //   

            setOrder((prev) => ({
                ...prev,
                orderItemDTOList: updatedOrderItems,
                totalPrice: newTotalPrice
            }));
            setSelectedCourseId(""); //   
        } else {
            console.log("   ID:", selectedCourseId); //   ID 
        }
    };


    const handleRemoveCourse = (courseId) => {
        const updatedOrderItems = order.orderItemDTOList.filter(item => item.courseId !== courseId);
        const newTotalPrice = updatedOrderItems.reduce((total, item) => total + item.price, 0);
        setOrder((prev) => ({...prev, orderItemDTOList: updatedOrderItems, totalPrice: newTotalPrice}));
    };

    const handleSubmit = async (event) => {
        event.preventDefault();
        try {
            const response = await axios.put(`http://localhost:8080/order/${orderId}`, order, {withCredentials: true});
            console.log("  :", response.data);
            navigate(`/orders/${orderId}`);
        } catch (error) {
            console.error("    :", error);
            setError("  .");
        }
    };

    if (!order) return <p> ...</p>;
    if (error) return <p style={{color: "red"}}>{error}</p>;

    return (
        <OrderUpdateContainer>
            <h2> </h2>
            <form onSubmit={handleSubmit}>
                <div>
                    <label> :</label>
                    <select onChange={handleCourseChange} value={selectedCourseId}>
                        <option value="" disabled> </option>
                        {courses.map(course => (
                            <option key={course.courseId} value={course.courseId}>
                                {course.courseName}
                            </option>
                        ))}
                    </select>
                    <button type="button" onClick={handleAddCourse}></button>
                </div>
                <OrderItems>
                    <h3> :</h3>
                    {order.orderItemDTOList.map(item => (
                        <OrderItem key={item.courseId}>
                            <span> ID: {item.courseId}</span>
                            <RemoveButton onClick={() => handleRemoveCourse(item.courseId)}></RemoveButton>
                        </OrderItem>
                    ))}
                </OrderItems>
                <div> : {order.totalPrice} </div>
                <button type="submit"> </button>
            </form>
        </OrderUpdateContainer>
    );
};

export default OrderUpdate; //  

const OrderUpdateContainer = styled.div`
    max-width: 600px;
    margin: 0 auto;
    padding: 1rem;
`;

const OrderItems = styled.div`
    margin-top: 1rem;
    border-top: 1px solid #ddd;
    padding-top: 1rem;
`;

const OrderItem = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0.5rem 0;
`;

const RemoveButton = styled.button`
    background-color: #dc3545;
    color: white;
    border: none;
    border-radius: 4px;
    padding: 0.5rem 1rem;
    cursor: pointer;

    &:hover {
        background-color: #c82333;
    }
`;
