import React from 'react';
import {Route, Routes} from 'react-router-dom';
import VideoList from './pages/video/VideoList'; //   
import VideoCreate from "./pages/video/VideoCreate";
import VideoUpdate from "./pages/video/VideoUpdate"; //   
import VideoInstructor from "./pages/video/VideoInstructor";

const VideoRoutes = () => (
    <Routes>
        <Route path="/:courseId" element={<VideoList/>}/>
        <Route path="/create/:courseId" element={<VideoCreate/>}/> {/*   */}
        <Route path="/update/:videoId" element={<VideoUpdate/>}/> {/*   */}
        <Route path="/Instructor/:courseId" element={<VideoInstructor/>}/>{/*  */}
    </Routes>
);

export default VideoRoutes;
