import React, {useEffect, useState} from 'react';
import {useNavigate, useParams} from 'react-router-dom';
import axiosInstance from "../axiosInstance";
import './Style/InquiryDetail.css';

const InquiryDetail = () => {
    const {inquiryId} = useParams();
    const [inquiry, setInquiry] = useState(null);
    const [answer, setAnswer] = useState(null);
    const [isInquiryEditing, setIsInquiryEditing] = useState(false); //   
    const [isAnswerCreating, setIsAnswerCreating] = useState(false); //   
    const [isAnswerEditing, setIsAnswerEditing] = useState(false); //   
    const [answerContent, setAnswerContent] = useState('');
    const [inquiryContent, setInquiryContent] = useState('');
    const [info, setInfo] = useState(null);
    const [inquiryAuthorId, setInquiryAuthorId] = useState(null); //  ID 
    const navigate = useNavigate();
    const [memberId, setMemberId] = useState(null);
    const [inquiryStatus] = useState('ANSWERED');

    // role  
    const getInfoFromToken = async () => {
        const accessToken = localStorage.getItem("accessToken");
        if (accessToken) {
            try {
                const response = await axiosInstance.get('/token/decode');
                setInfo(response.data);
                setMemberId(response.data.mid)
            } catch (error) {
                console.error('Failed to get role:', error);
            }
        }
    };

    //    
    const fetchInquiry = async () => {
        try {
            const response = await axiosInstance.get(`/inquiries/${inquiryId}`);
            setInquiry(response.data);
            setInquiryContent(response.data.inquiryContent); //    
            setInquiryAuthorId(response.data.memberId); //  ID 
        } catch (error) {
            console.error('Failed to fetch inquiry:', error);
        }
    };

    const fetchAnswer = async () => {
        try {
            const response = await axiosInstance.get(`/answers/${inquiryId}`);
            if (response.data && response.data.answerContent) {
                setAnswer(response.data);
            }
        } catch (error) {
            setAnswer(null);
        }
    };

    useEffect(() => {
        getInfoFromToken();
    }, []);

    useEffect(() => {
        fetchInquiry();
        fetchAnswer();
    }, [memberId]);

    //   
    const handleAnswerSave = async () => {
        try {
            await axiosInstance.post('/answers', {
                inquiryId: inquiryId,
                answerContent: answerContent,
            });
            await axiosInstance.put(`/inquiries/${inquiryId}/status`, {
                inquiryStatus: inquiryStatus,
            });
            setAnswerContent('');
            setIsAnswerCreating(false);
            fetchAnswer(); //  
            fetchInquiry();
        } catch (error) {
            console.error('Failed to save answer:', error);
        }
    };

    //   
    const handleAnswerEdit = () => {
        if (answer) {
            setIsAnswerEditing(true);
            setAnswerContent(answer.answerContent);
        }
    };

    const handleAnswerUpdate = async () => {
        try {
            await axiosInstance.put(`/answers/${inquiryId}`, {
                answerContent: answerContent,
            });
            setAnswerContent('');
            setIsAnswerEditing(false);
            fetchAnswer(); //  
        } catch (error) {
            console.error('Failed to update answer:', error);
        }
    };

    //   
    const handleAnswerDelete = async () => {
        const confirmDelete = window.confirm(' ?');
        if (confirmDelete) {
            try {
                await axiosInstance.delete(`/answers/${inquiryId}`);
                setAnswer(null);
                setAnswerContent('')
                await axiosInstance.put(`/inquiries/${inquiryId}/status`, {
                    inquiryStatus: 'CONFIRMING',
                })
                fetchAnswer(); //  
                fetchInquiry();
            } catch (error) {
                console.error('Failed to delete answer:', error);
            }
        }
    };

    //   
    const handleInquiryEdit = () => {
        setIsInquiryEditing(true);
    };

    const handleInquiryUpdate = async () => {
        try {
            await axiosInstance.put(`/inquiries/${inquiryId}`, {
                inquiryTitle: inquiry.inquiryTitle,
                inquiryContent: inquiryContent,
            });
            setInquiryContent(inquiryContent);
            setIsInquiryEditing(false);
            fetchInquiry(); //  
        } catch (error) {
            console.error('Failed to update inquiry:', error);
        }
    };

    //   
    const handleInquiryDelete = async () => {
        const confirmDelete = window.confirm(' ?');
        if (confirmDelete) {
            try {
                await axiosInstance.delete(`/inquiries/${inquiryId}`);
                navigate('/inquiries'); //     
            } catch (error) {
                console.error('Failed to delete inquiry:', error);
            }
        }
    };

    //   
    const handleBackToList = () => {
        navigate('/inquiries');
    };

    const isButtonDisabled1 = answerContent.trim() === ''; //  
    const isButtonDisabled2 = inquiryContent.trim() === ''; //  


    if (!inquiry) return <p> ...</p>;

    return (
        <div className="inquiry-detail">
            <h1>  </h1>

            <div className="inquiry-area">
                <h2>{inquiry.inquiryTitle}</h2>
                <div className="divider"></div>
                <div className="inquiry-info">
                    <span><strong>:</strong> {inquiry.memberNickname}</span>
                    <span><strong>:</strong> {new Date(inquiry.inquiryCreateDate).toLocaleDateString()}</span>
                    <span><strong>:</strong> {new Date(inquiry.inquiryUpdateDate).toLocaleDateString()}</span>
                    <span><strong>:</strong> {inquiry.inquiryStatus}</span>
                </div>
                <div className="divider"></div>
                <div className="inquiry-content">
                    <strong>:</strong>
                    {isInquiryEditing ? (
                        <div>
                            <textarea
                                typeof={"text"}
                                value={inquiryContent}
                                onChange={(e) => setInquiryContent(e.target.value)}
                                placeholder="  "
                                required
                            />
                            <button onClick={handleInquiryUpdate} disabled={isButtonDisabled2}></button>
                            <button onClick={() => setIsInquiryEditing(false)}></button>
                        </div>
                    ) : (
                        <p>{inquiryContent}</p>
                    )}
                </div>
                {parseInt(inquiryAuthorId, 10) === parseInt(memberId, 10) && !isInquiryEditing && ( //  ID 
                    <div className="inquiry-actions">
                        <button onClick={handleInquiryEdit}></button>
                        <button onClick={handleInquiryDelete}></button>
                    </div>
                )}
            </div>

            <div className="answer-area">
                <h2> </h2>
                <div className="divider"></div>
                {answer ? (
                    <div>
                        <strong> :</strong>
                        {isAnswerEditing ? (
                            <div>
                                <textarea
                                    value={answerContent}
                                    onChange={(e) => setAnswerContent(e.target.value)}
                                    placeholder=" "
                                    required
                                />
                                <button onClick={handleAnswerUpdate} disabled={isButtonDisabled1}></button>
                                <button onClick={() => setIsAnswerEditing(false)}></button>
                            </div>
                        ) : (
                            <p>{answer.answerContent}</p>
                        )}
                        {info.role === 'ROLE_ADMIN' && !isAnswerEditing && (
                            <div className="answer-actions">
                                <button onClick={handleAnswerEdit}></button>
                                <button onClick={handleAnswerDelete}></button>
                            </div>
                        )}
                    </div>
                ) : (
                    <div>
                        {info.role === 'ROLE_ADMIN' && !isAnswerCreating && (
                            <>
                                <p> .</p>
                                <button onClick={() => setIsAnswerCreating(true)}> </button>
                            </>
                        )}
                    </div>
                )}
                {isAnswerCreating && (
                    <div>
                        <textarea
                            value={answerContent}
                            onChange={(e) => setAnswerContent(e.target.value)}
                            placeholder=" "
                            required={true}
                        />
                        <button onClick={handleAnswerSave} disabled={isButtonDisabled2}></button>
                        <button onClick={() => setIsAnswerCreating(false)}></button>
                    </div>
                )}
            </div>

            <button className="back-button" onClick={handleBackToList}> </button>
        </div>
    );
};

export default InquiryDetail;
