import React, {useEffect, useState} from "react";
import {Link, useNavigate} from "react-router-dom";
import axiosInstance from './axiosInstance'; // axiosInstance import

const CourseNewsList = ({courseId}) => {
    const [newsList, setNewsList] = useState([]);
    const [currentPage, setCurrentPage] = useState(0);
    const [totalPages, setTotalPages] = useState(0);
    const [userRole, setUserRole] = useState(null);
    const [userName, setUserName] = useState(null);
    const [instructorName, setInstructorName] = useState(null);
    const [isLoading, setIsLoading] = useState(true);
    const newsPerPage = 5;
    const navigate = useNavigate();

    //   
    const fetchInstructorName = async () => {
        try {
            const response = await axiosInstance.get(`/course/${courseId}/member-nickname`);
            setInstructorName(response.data);
        } catch (err) {
            console.error("Failed to fetch instructor nickname:", err);
        } finally {
            setIsLoading(false);
        }
    };

    //    ( )
    const checkUserRole = async () => {
        // console.log("Checking user role...");
        try {
            //   JWT  
            const token = localStorage.getItem('accessToken');
            // console.log("Token:", token);
            if (token) {
                // Authorization  JWT  
                const response = await axiosInstance.get('/token/decode', {
                    headers: {
                        Authorization: `Bearer ${token}`
                    }
                });

                //       
                setUserRole(response.data.role);  // role 
                setUserName(response.data.username);   //  ID 
                console.log("User Role:", response.data.role);
                // console.log("User Name:", response.data.mid);
            }
        } catch (error) {
            console.error("    :", error);
        }
    };

    //   
    const fetchNews = (page) => {
        fetch(`http://localhost:8080/course/${courseId}/news?page=${page}&size=${newsPerPage}`)
            .then(res => res.json())
            .then(data => {
                setNewsList(data.content);
                setTotalPages(data.totalPages);
            })
            .catch(err => console.error("  :", err));
    };

    //          
    useEffect(() => {
        checkUserRole();
        fetchInstructorName();
    }, [courseId]);

    //      
    useEffect(() => {
        fetchNews(currentPage);
    }, [courseId, currentPage]);

    //           
    const canCreateNews = () => {
        return (userRole === 'ROLE_INSTRUCTOR' && userName === instructorName) ||
            userRole === 'ROLE_ADMIN';
    };

    //   
    const handlePrevPage = () => {
        if (currentPage > 0) {
            setCurrentPage(currentPage - 1);
        }
    };

    //   
    const handleNextPage = () => {
        if (currentPage < totalPages - 1) {
            setCurrentPage(currentPage + 1);
        }
    };

    //      
    const handleCreateNews = () => {
        if (isLoading) return;  //     

        if (canCreateNews()) {
            navigate(`/courses/${courseId}/news/create`);
        } else {
            alert('     .');
        }
    };

    return (
        <div style={styles.newsContainer}>
            <h2 style={styles.newsHeader}> </h2>

            {canCreateNews() && (
                <button onClick={handleCreateNews} style={styles.createNewsButton}>
                     
                </button>
            )}

            <ul style={styles.newsList}>
                {newsList.map(news => (
                    <li key={news.newsId} style={styles.newsItem}>
                        <Link
                            to={`/courses/${courseId}/news/${news.newsId}`}
                            style={styles.newsLink}
                        >
                            <span style={styles.newsTitle}>{news.newsName}</span>
                            <span style={styles.newsArrow}></span>
                        </Link>
                    </li>
                ))}
            </ul>

            <div style={styles.pagination}>
                <button
                    onClick={handlePrevPage}
                    disabled={currentPage === 0}
                    style={currentPage === 0 ? {...styles.paginationButton, ...styles.disabledButton} : styles.paginationButton}
                >
                    
                </button>

                <span>{currentPage + 1} / {totalPages}</span>

                <button
                    onClick={handleNextPage}
                    disabled={currentPage === totalPages - 1}
                    style={currentPage === totalPages - 1 ? {...styles.paginationButton, ...styles.disabledButton} : styles.paginationButton}
                >
                    
                </button>
            </div>
        </div>
    );
};

const styles = {
    newsContainer: {
        maxWidth: '900px',
        margin: '40px auto',
        padding: '30px',
        backgroundColor: '#fff',
        borderRadius: '16px',
        boxShadow: '0 4px 16px rgba(0, 0, 0, 0.1)',
        transition: 'transform 0.3s ease',
    },
    newsHeader: {
        fontSize: '32px',
        color: '#333',
        marginBottom: '30px',
        borderBottom: '3px solid #007bff',
        paddingBottom: '15px',
        fontWeight: 'bold',
    },
    createNewsButton: {
        padding: '12px 20px',
        backgroundColor: '#007bff',
        color: 'white',
        border: 'none',
        borderRadius: '30px',
        cursor: 'pointer',
        fontSize: '16px',
        fontWeight: 'bold',
        letterSpacing: '1px',
        transition: 'background-color 0.3s ease, transform 0.2s ease',
        marginBottom: '30px',
    },
    createNewsButtonHover: {
        backgroundColor: '#0056b3',
        transform: 'scale(1.05)',
    },
    newsList: {
        listStyleType: 'none',
        padding: 0,
        marginBottom: '20px',
    },
    newsItem: {
        marginBottom: '20px',
        transition: 'transform 0.3s ease',
    },
    newsItemHover: {
        transform: 'translateY(-5px)',
    },
    newsLink: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        padding: '20px',
        backgroundColor: '#f9f9f9',
        borderRadius: '10px',
        color: '#333',
        textDecoration: 'none',
        boxShadow: '0 2px 8px rgba(0, 0, 0, 0.05)',
        transition: 'box-shadow 0.3s ease, transform 0.3s ease',
    },
    newsLinkHover: {
        boxShadow: '0 6px 12px rgba(0, 0, 0, 0.1)',
        transform: 'translateY(-5px)',
    },
    newsTitle: {
        fontWeight: 'bold',
        fontSize: '18px',
    },
    newsArrow: {
        fontSize: '20px',
        color: '#007bff',
        transition: 'transform 0.3s ease',
    },
    pagination: {
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        marginTop: '30px',
    },
    paginationButton: {
        padding: '12px 20px',
        backgroundColor: '#007bff',
        color: 'white',
        border: 'none',
        borderRadius: '30px',
        cursor: 'pointer',
        fontSize: '16px',
        transition: 'background-color 0.3s ease, transform 0.3s ease',
        fontWeight: 'bold',
    },
    paginationButtonHover: {
        backgroundColor: '#0056b3',
        transform: 'scale(1.05)',
    },
    disabledButton: {
        backgroundColor: '#ccc',
        cursor: 'not-allowed',
    },
    paginationText: {
        fontSize: '16px',
        fontWeight: 'bold',
        color: '#333',
    },
};


export default CourseNewsList;