// Instructor.js
import React, {useEffect, useState} from "react";
import {useParams} from "react-router-dom";
import axios from "axios";
import styled from "styled-components";
import InstructorReview from "./instructor-review/InstructorReview"; // Import your list

const defaultImage = "/images/instructor_default_img.png";

const Instructor = () => {
    const {nickname} = useParams();
    const [instructor, setInstructor] = useState(null);
    const [reviews, setReviews] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const token = localStorage.getItem("Authorization"); //    

        //   
        axios.get(`http://localhost:8080/members/instructor/${nickname}`, {
            headers: {
                Authorization: `Bearer ${token}` // Authorization   
            },
            credentials: 'include',
        })
            .then((response) => {
                setInstructor(response.data);
                setLoading(false);
            })
            .catch((error) => {
                setError("     .");
                setLoading(false);
            });

        //    
        axios.get(`http://localhost:8080/members/instructor/${nickname}/reviews/list`, {
            headers: {
                Authorization: `Bearer ${token}` //      
            },
            credentials: 'include',
        })
            .then((response) => {
                setReviews(response.data);
            })
            .catch((error) => {
                setError("     .");
            });
    }, [nickname]);


    if (loading) {
        return <p> ...</p>;
    }

    if (error) {
        return <p>{error}</p>;
    }

    const profileImageSrc = instructor?.profileImage
        ? `data:image/jpeg;base64,${instructor.profileImage}`
        : "http://localhost:8080/images/default_profile.jpg";

    return (
        <PageContainer>
            {instructor && (
                <InstructorInfo>
                    <InstructorImage
                        src={profileImageSrc} // Base64   
                        alt="Instructor Image"
                    />
                    <InstructorDetails>
                        <InstructorName>{instructor.nickname}</InstructorName>
                        <InstructorBio>{instructor.introduction}</InstructorBio>
                    </InstructorDetails>
                </InstructorInfo>
            )}
            {/*   InstructorReviewList  */}
            <InstructorReview reviews={reviews}/>
        </PageContainer>
    );
};

export default Instructor;

//  

const PageContainer = styled.div`
    max-width: 800px;
    margin: 0 auto;
    padding: 1rem;
`;

const InstructorInfo = styled.div`
    display: flex;
    align-items: center;
    border: 1px solid #ddd;
    padding: 1rem;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    margin-bottom: 2rem; /*        */
`;

const InstructorImage = styled.img`
    width: 200px;
    height: 200px;
    object-fit: cover;
    border-radius: 50%;
`;

const InstructorDetails = styled.div`
    margin-left: 1rem;
`;

const InstructorName = styled.h2`
    font-size: 2rem;
    margin: 0; /*     */
`;

const InstructorBio = styled.p`
    font-size: 1.2rem;
    color: #555;
    margin: 0; /*     */
`;
