import React, {useEffect, useState} from 'react'; // React   
import {useNavigate} from 'react-router-dom'; //    useNavigate  
import './Style/InquiryRegistration.css'
import axiosInstance from "../axiosInstance";

const InquiryRegistration = () => {
    const [inquiryTitle, setInquiryTitle] = useState(''); //   
    const [inquiryContent, setInquiryContent] = useState(''); //   
    const navigate = useNavigate(); //    navigate  
    const [memberId, setMemberId] = useState(null);

    const getInfoFromToken = async () => {
        const accessToken = localStorage.getItem("accessToken");
        if (accessToken) {
            try {
                const response = await axiosInstance.get('/token/decode');
                setMemberId(response.data.mid)
            } catch (error) {
                console.error('Failed to get role:', error);
            }
        }
    };

    useEffect(() => {
        getInfoFromToken();
    }, []);

    //      
    const handleSubmit = async (e) => {
        e.preventDefault(); //     

        // memberId      
        if (memberId === null) {
            alert(' .'); //    
            navigate('/login'); //   
            return;
        }

        //    
        const inquiryDTO = {
            inquiryTitle,
            inquiryContent,
            memberId: Number(memberId), // memberId   
        };

        try {
            //     
            await axiosInstance.post('/inquiries', inquiryDTO);
            alert(' .'); //   
            navigate('/inquiries'); //    
        } catch (error) {
            //        
            console.error('Failed to register inquiry:', error.response ? error.response.data : error.message); //      
            alert('  .'); //   
        }
    };

    const handleCancel = () => {
        navigate('/inquiries'); //   
    };

    return (
        <div className="inquiry-registration">
            <h1> </h1>
            <form onSubmit={handleSubmit}> {/*    handleSubmit   */}
                <div>
                    <label></label>
                    <input
                        type="text"
                        value={inquiryTitle}
                        onChange={(e) => setInquiryTitle(e.target.value)} //   
                        required //  
                    />
                </div>
                <div>
                    <label></label>
                    <textarea
                        value={inquiryContent}
                        onChange={(e) => setInquiryContent(e.target.value)} //   
                        required //  
                    />
                </div>
                <div className="button-group">
                    <button type="submit" className="submit-button"> </button>
                    <button type="button" className="cancel-button" onClick={handleCancel}></button>
                </div>
            </form>
        </div>
    );
};

export default InquiryRegistration;
