import React, {useEffect, useState} from "react";
import {useNavigate, useParams} from "react-router-dom";
import axios from "../axiosInstance";
import axiosInstance from "../axiosInstance";
import styled from "styled-components";

const OrderDetail = () => {
    const {orderId} = useParams();
    const [order, setOrder] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState(null);
    const navigate = useNavigate(); // useNavigate  

    const [memberId, setMemberId] = useState(null);


    //    memberId  ( )
    const checkUserRole = async () => {
        try {
            const token = localStorage.getItem('accessToken');

            if (token) {
                // Authorization  JWT  
                const response = await axiosInstance.get('/token/decode', {
                    headers: {
                        Authorization: `Bearer ${token}`
                    }
                });

                //       
                setMemberId(response.data.mid);    // memberId 
            }
        } catch (error) {
            console.error("    :", error);
        }
    };
    useEffect(() => {
        checkUserRole();
        const fetchOrderDetail = async () => {
            setLoading(true);
            try {
                const response = await axios.get(`http://localhost:8080/order/${orderId}`, {withCredentials: true});
                setOrder(response.data);
            } catch (error) {
                console.error("     :", error);
                setError("    .");
            } finally {
                setLoading(false);
            }
        };

        fetchOrderDetail();
    }, [orderId]);

    if (loading) return <p> ...</p>;
    if (error) return <p>{error}</p>;
    if (!order) return <p> .</p>;

    const handleBackClick = () => {
        navigate("/orders"); // /orders/list 
    };

    return (
        <OrderDetailContainer>
            <h2> </h2>
            <p> ID: {order.orderId}</p>
            <p> : {order.orderStatus}</p>
            <p> : {order.totalPrice} </p>
            <p> :</p>
            <ul>
                {order.orderItemDTOList.map((item, index) => (
                    <li key={index}>
                         ID: {item.courseId},  : {item.courseName}, : {item.price} 
                    </li>
                ))}
            </ul>

            <BackButton onClick={handleBackClick}></BackButton>
        </OrderDetailContainer>
    );
};

export default OrderDetail;

const OrderDetailContainer = styled.div`
    max-width: 600px;
    margin: 0 auto;
    padding: 1rem;
`;

const BackButton = styled.button`
    margin-top: 1rem;
    padding: 0.5rem 1rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;

    &:hover {
        background-color: #0056b3;
    }
`;
