import React, {useEffect, useState} from "react";
import axios from "../axiosInstance";
import axiosInstance from "../axiosInstance"; // axiosInstance import 
import {useNavigate} from "react-router-dom";
import styled from "styled-components";

const Course_Url = "http://localhost:8080/course";

const CourseCreate = () => {
    const [courseName, setCourseName] = useState("");
    const [courseDescription, setCourseDescription] = useState("");
    const [coursePrice, setCoursePrice] = useState(0);
    const [courseLevel, setCourseLevel] = useState(1); //  1
    const [courseAttribute, setCourseAttribute] = useState("");
    const [memberNickname, setMemberNickname] = useState(null); // memberNickname  
    const [error, setError] = useState(null);
    const navigate = useNavigate();

    //    memberNickname  ( )
    const checkUserRole = async () => {
        try {
            const token = localStorage.getItem('accessToken');

            if (token) {
                const response = await axiosInstance.get('/token/decode', {
                    headers: {
                        Authorization: `Bearer ${token}`
                    }
                });
                console.log(" :", response.data); //   
                setMemberNickname(response.data.username); // memberNickname 
            } else {
                console.error("  .");
                setError(" .");
            }
        } catch (error) {
            console.error("    :", error);
            setError("    .");
        }
    };

    //      
    useEffect(() => {
        checkUserRole();
    }, []);

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (!memberNickname) {
                throw new Error("    .");
            }

            const payload = {
                courseName,
                courseDescription,
                coursePrice,
                courseLevel,
                courseAttribute,
                memberNickname, // memberNickname 
            };

            await axios.post(Course_Url, payload, {withCredentials: true});

            //    
            alert("  .");
            navigate("/courses/list");
        } catch (err) {
            setError("  .");
        }
    };

    return (
        <Container>
            <h2> </h2>
            {error && <ErrorMessage>{error}</ErrorMessage>}
            <form onSubmit={handleSubmit}>
                <Label>
                     :
                    <Input
                        type="text"
                        value={courseName}
                        onChange={(e) => setCourseName(e.target.value)}
                        required
                    />
                </Label>
                <Label>
                     :
                    <Input
                        type="text"
                        value={memberNickname}
                        onChange={(e) => setMemberNickname(e.target.value)}
                        required
                    />
                </Label>
                <Label>
                     :
                    <Input
                        type="text"
                        value={courseDescription}
                        onChange={(e) => setCourseDescription(e.target.value)}
                        required
                    />
                </Label>

                <Label>
                     :
                    <Input
                        type="number"
                        value={coursePrice}
                        onChange={(e) => setCoursePrice(Number(e.target.value))}
                        required
                    />
                </Label>

                <Label>
                     :
                    <Input
                        type="number"
                        value={courseLevel}
                        onChange={(e) => setCourseLevel(Number(e.target.value))}
                        min="1"
                        max="5"
                        required
                    />
                </Label>
                <Label>
                     :
                    <Input
                        type="text"
                        value={courseAttribute}
                        onChange={(e) => setCourseAttribute(e.target.value)}
                        required
                    />
                </Label>

                <Button type="submit"></Button>
            </form>
        </Container>
    );
};

export default CourseCreate;

const Container = styled.div`
    max-width: 400px;
    margin: 0 auto;
    padding: 2rem;
    background: #f9f9f9;
    border-radius: 8px;
`;

const Label = styled.label`
    display: block;
    margin-bottom: 1rem;
`;

const Input = styled.input`
    width: 100%;
    padding: 0.5rem;
    margin-top: 0.5rem;
`;

const Button = styled.button`
    padding: 0.5rem 1rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
`;

const ErrorMessage = styled.p`
    color: red;
    font-weight: bold;
`;
