import React, {useState} from 'react';
import {Link, useLocation} from 'react-router-dom';

function Sidebar() {
    const location = useLocation();
    const [selectedMenu, setSelectedMenu] = useState(location.pathname); //    

    const handleMenuClick = (path) => {
        setSelectedMenu(path);
    };

    const menuItems = [
        {path: "/admin/courses/create", label: " "},
        {path: "/admin/courses-management", label: " "},
        {path: "/admin/inquiries", label: " "},
        {path: "/admin/members", label: " "},
    ];

    return (
        <div style={sidebarStyle}>
            <h2 style={titleStyle}> </h2>
            <ul style={listStyle}>
                {menuItems.map((item) => (
                    <li key={item.path}>
                        <Link
                            to={item.path}
                            style={{...linkStyle, color: selectedMenu === item.path ? '#007bff' : 'black'}}
                            onClick={() => handleMenuClick(item.path)}
                        >
                            {item.label}
                        </Link>
                    </li>
                ))}
            </ul>
        </div>
    );
}

const sidebarStyle = {
    width: '200px',
    backgroundColor: '#f4f4f4',
    padding: '20px',
    height: '100%',
};

const titleStyle = {
    fontSize: '25px', //   20px 
    fontWeight: 'bold',
    marginBottom: '20px',
    paddingBottom: '10px', //   
    borderBottom: '2px solid #ccc', //  
};

const listStyle = {
    listStyleType: 'none',
    paddingLeft: 0,
};

const linkStyle = {
    textDecoration: 'none',
    display: 'block',
    padding: '5px 0',
    fontSize: '20px', //   16px 
};

export default Sidebar;
