import React, {useEffect, useState} from 'react';
import {useNavigate} from 'react-router-dom';
import './Style/InquiryList.css';
import axiosInstance from "../axiosInstance";

const InquiryListPage = () => {
    const [inquiries, setInquiries] = useState([]);
    const [sortBy, setSortBy] = useState('inquiryId');
    const [filterStatus, setFilterStatus] = useState('ALL');
    const [memberId, setMemberId] = useState(null);
    const [isMyInquiries, setIsMyInquiries] = useState(false); //    
    const navigate = useNavigate();

    const getInfoFromToken = async () => {
        const accessToken = localStorage.getItem("accessToken");
        if (accessToken) {
            try {
                const response = await axiosInstance.get('/token/decode');
                setMemberId(response.data.mid)
            } catch (error) {
                console.error('Failed to get role:', error);
            }
        }
    };
    useEffect(() => {
        getInfoFromToken();
    }, []);

    useEffect(() => {
        const fetchInquiries = async () => {
            try {
                const response = await axiosInstance.get('/inquiries');
                setInquiries(response.data);
            } catch (error) {
                console.error('Failed to fetch inquiries:', error.response ? error.response.data : error.message);
            }
        };

        if (memberId !== null) {
            fetchInquiries();
        }
    }, [memberId]);

    //    
    const fetchMyInquiries = async () => {
        if (memberId === null) return; // memberId  

        try {
            const response = await axiosInstance.get(`/inquiries/member/${memberId}`); // memberId  
            setInquiries(response.data);
            setIsMyInquiries(true); //     
        } catch (error) {
            console.error('Failed to fetch my inquiries:', error.response ? error.response.data : error.message);
        }
    };

    //    
    const fetchAllInquiries = async () => {
        try {
            const response = await axiosInstance.get('/inquiries');
            setInquiries(response.data);
            setIsMyInquiries(false); //   
        } catch (error) {
            console.error('Failed to fetch inquiries:', error.response ? error.response.data : error.message);
        }
    };

    const handleSortChange = (e) => {
        setSortBy(e.target.value);
    };

    const handleStatusFilterChange = (e) => {
        setFilterStatus(e.target.value);
    };

    const filteredInquiries = inquiries.filter((inquiry) => {
        if (filterStatus === 'ALL') return true;
        return inquiry.inquiryStatus === filterStatus;
    });

    const sortedInquiries = filteredInquiries.sort((a, b) => {
        if (sortBy === 'status') {
            return a.inquiryStatus.localeCompare(b.inquiryStatus);
        } else if (sortBy === 'inquiryId') {
            return b.inquiryId - a.inquiryId; //  
        }
        return 0;
    });

    return (
        <div className="inquiry-list-page">
            <h1> </h1>
            <div className="actions">
                <label>
                     :
                    <select value={sortBy} onChange={handleSortChange}>
                        <option value="latest"></option>
                        <option value="status"></option>
                    </select>
                </label>
                <label>
                     :
                    <select value={filterStatus} onChange={handleStatusFilterChange}>
                        <option value="ALL"></option>
                        <option value="CONFIRMING">CONFIRMING</option>
                        <option value="PENDING">PENDING</option>
                        <option value="ANSWERED">ANSWERED</option>
                        <option value="RESOLVED">RESOLVED</option>
                    </select>
                </label>
                {/*     */}
                <button onClick={isMyInquiries ? fetchAllInquiries : fetchMyInquiries}>
                    {isMyInquiries ? '  ' : '  '}
                </button>
                {memberId && (
                    <button onClick={() => navigate('/inquiries/new')}> </button>
                )}
            </div>

            {sortedInquiries.length === 0 ? (
                <p>  .</p>
            ) : (
                <table>
                    <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                    </thead>
                    <tbody>
                    {sortedInquiries.map((inquiry) => (
                        <tr
                            key={inquiry.inquiryId}
                            onClick={() => navigate(`/inquiries/${inquiry.inquiryId}`)}
                        >
                            <td>{inquiry.inquiryId}</td>
                            <td>{inquiry.inquiryTitle}</td>
                            <td>{inquiry.memberNickname}</td>
                            <td>{inquiry.inquiryStatus}</td>
                            <td>{new Date(inquiry.inquiryCreateDate).toLocaleDateString()}</td>
                            <td>{new Date(inquiry.inquiryUpdateDate).toLocaleDateString()}</td>
                        </tr>
                    ))}
                    </tbody>
                </table>
            )}
        </div>
    );
};

export default InquiryListPage;
