import React, {useEffect, useState} from "react";
import styled from "styled-components";
import axiosInstance from '../pages/axiosInstance';

const EditProfile = () => {
    // React Hook   
    const [userInfo, setUserInfo] = useState({
        nickname: "",
        email: "",
        introduction: "",
        password: "",
    });
    const [isOAuthUser, setIsOAuthUser] = useState(false);
    const [errorMessage, setErrorMessage] = useState("");
    const [successMessage, setSuccessMessage] = useState("");
    const [currentPassword, setCurrentPassword] = useState("");
    const [isPasswordVerified, setIsPasswordVerified] = useState(false);
    const [setMid] = useState(null);

    useEffect(() => {
        const fetchUserData = async () => {
            try {
                const response = await axiosInstance.get('/token/decode');
                const {mid} = response.data;
                setMid(mid);

                if (!mid) {
                    console.error(" ID .");
                    return;
                }

                const userInfoResponse = await axiosInstance.get(`/members/${mid}`);

                //    
                if (userInfoResponse.status === 200) {
                    const data = userInfoResponse.data; //  
                    setUserInfo(data);

                    // OAuth  
                    setIsOAuthUser(data.password.includes("naver") || data.password.includes("google"));
                } else {
                    console.error("   :", userInfoResponse.status);
                }
            } catch (error) {
                console.error("API    :", error);
            }
        };

        fetchUserData();
    }, []);


    const handleChange = (e) => {
        const {name, value} = e.target;
        setUserInfo((prevUserInfo) => ({
            ...prevUserInfo,
            [name]: value,
        }));
    };

    const handleVerifyPassword = async () => {
        const memberId = localStorage.getItem("memberId");

        try {
            const response = await fetch(`http://localhost:8080/members/${memberId}/verify-password`, {
                method: "POST",
                headers: {
                    "Content-Type": "text/plain",
                },
                body: currentPassword,
                credentials: "include",
            });

            if (response.ok) {
                setIsPasswordVerified(true);
                setErrorMessage("");
                setSuccessMessage("  !    .");
            } else {
                const errorBody = await response.text();
                setErrorMessage(`  : ${errorBody || "    "}`);
                setIsPasswordVerified(false);
            }
        } catch (error) {
            console.error("    :", error);
            setErrorMessage("    ");
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        if (isOAuthUser) {
            setIsPasswordVerified(true); // OAuth     
        } else if (!isPasswordVerified) {
            setErrorMessage("  .");
            return;
        }

        const memberId = localStorage.getItem("memberId");

        try {
            const response = await fetch(`http://localhost:8080/members/${memberId}`, {
                method: "PUT",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({
                    nickname: userInfo.nickname,
                    email: userInfo.email,  //    
                    introduction: userInfo.introduction,
                    password: isPasswordVerified ? userInfo.password : undefined, //      
                }),
                credentials: "include",
            });

            if (response.ok) {
                setSuccessMessage("   .");
                setErrorMessage("");

                //   
                alert("   .");
                window.location.href = "http://localhost:3000/";
            } else {
                const errorBody = await response.text();
                setErrorMessage(` : ${errorBody || "    "}`);
                setSuccessMessage("");
            }
        } catch (error) {
            console.error("     :", error);
            setErrorMessage("     ");
            setSuccessMessage("");
        }
    };

    return (
        <Container>
            <Title> </Title>
            <Form onSubmit={handleSubmit}>
                <Label>
                    :
                    <Input type="text" name="nickname" value={userInfo.nickname} onChange={handleChange} required/>
                </Label>
                <Label>
                    :
                    <Input
                        type="email"
                        name="email"
                        value={userInfo.email}
                        readOnly={true}  //   
                    />
                </Label>
                <Label>
                    :
                    <Textarea name="introduction" value={userInfo.introduction} onChange={handleChange}/>
                </Label>
                {!isOAuthUser && (
                    <>
                        <Label>
                             :
                            <Input
                                type="password"
                                value={currentPassword}
                                onChange={(e) => setCurrentPassword(e.target.value)}
                                required
                            />
                            <VerifyButton type="button" onClick={handleVerifyPassword}> </VerifyButton>
                        </Label>
                        <Label>
                             :
                            <Input
                                type="password" //      type password 
                                name="password"
                                onChange={handleChange}
                                placeholder="  ."
                            />
                        </Label>
                    </>
                )}
                <Button type="submit"></Button>
            </Form>
            {errorMessage && <ErrorMessage>{errorMessage}</ErrorMessage>}
            {successMessage && <SuccessMessage>{successMessage}</SuccessMessage>}
        </Container>
    );
};

export default EditProfile;

//  
const Container = styled.div`
    padding: 4rem;
    max-width: 600px;
    margin: auto;
    text-align: center;
`;

const Title = styled.h1`
    font-size: 2.5rem;
    margin-bottom: 2rem;
`;

const Form = styled.form`
    display: flex;
    flex-direction: column;
    gap: 1rem;
`;

const Label = styled.label`
    text-align: left;
`;

const Input = styled.input`
    width: 100%;
    padding: 0.5rem;
    border-radius: 5px;
    border: 1px solid #ddd;
`;

const Textarea = styled.textarea`
    width: 100%;
    padding: 0.5rem;
    border-radius: 5px;
    border: 1px solid #ddd;
    resize: none;
`;

const Button = styled.button`
    background-color: #3cb371;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    cursor: pointer;
    font-size: 1.2rem;

    &:hover {
        background-color: #218838;
    }
`;

const VerifyButton = styled.button`
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    cursor: pointer;
    font-size: 1.2rem;
    margin-top: 0.5rem;

    &:hover {
        background-color: #0056b3;
    }
`;

const ErrorMessage = styled.div`
    color: red;
    font-size: 1.2rem;
    margin-top: 1rem;
`;

const SuccessMessage = styled.div`
    color: green;
    font-size: 1.2rem;
    margin-top: 1rem;
`;
